.chartContainer {
  width: device-width;
  height: 100%;
}

.chartPage {
  width: device-width;
  height: 900px;
}

.chartPage2 {
  width: 100%;
  height: 11000px;
}

.btn {
  font-size: 1.2rem;
  min-width: 90px;
  padding: 15px 10px;
  margin: 5px;
}

.vowelDiv {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.crimeCaseList {
  display: flex;
  flex-direction: row;
}

.crimeTextColumn {
  display: flex;
  flex-direction: column;
}


/* 검색 창 */

.input-container {
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input {
  height: 64px;
  width: 50vw;
  min-width: 400px;
  font-size: 24px;
}

.board {
  margin: auto;
  width: 90vw;
  border: 1px solid black;
}

.boardItem {
  height: 64px;
  margin: 15px;
  padding: 0px 10px;
  background-color: lightblue;
  
  font-size: 24px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}